const ERC721Assignment = () => {
  const { currentAccount, provider } = useContext(BlockchainContext);
  const [contract, setContract] = useState();
  useEffect(() => {
    /*
     *  ethers.js  provider, contractAddress  contractABI  Contract 
     *  Contract  contract state
     * :  provider
     * 1.  signer Contract  connect  signer
     * 2.  block ()  gas fee Contract  gasLimit 
     * : https://docs.ethers.io/v5/getting-started/#getting-started--contracts
     */
    if (!provider) {
      return;
    }

    const signer = provider.getSigner();

    provider.getBlock().then((block) => {  

      const contract_ = new ethers.Contract(
        contractAddress,
        contractABI,
        provider,
        { gasLimit: block.getLimit }
      ); 
      setContract(contract_.connect(signer));
    });
  }, [provider]);

  const [totalSupply, setTotalSupply] = useState();
  useEffect(() => {
    /*
     * :
     *  contract state  contract state totalSupply 
     *  totalSupply state 
     *  <div> Mint : {totalSupply}</div>  totalSupply 
     * :  ethers.js  counter  bigNumber
     */
    if (!contract) {
      return;
    }

    const getTotalSupply = async () => {
      const totalSupply_ = await contract.totalSupply();
      setTotalSupply(totalSupply_?.toNumber());
    };

    getTotalSupply();

  }, [contract]);

  const [mintPrice, setPrice] = useState();
  useEffect(() => {
    /*
     * :
     *  contract state  contract state mintPrice 
     *  mintPrice state 
     *  <div>Mint : {totalSupply} ETH</div>  mint 
     * :
     * 1.  ethers.js  mintPrice  bigNumber
     * 2. mintPrice  0.01 ether ether  wei  js Number 
     *     ethers.utils.formatEther  ()
     */

    if (!contract) {
      return;
    }

    const getMintPrice = async() => {
      const mintPrice_ = await contract.mintPrice();
      setPrice(ethers.utils.formatEther(mintPrice_))
    };

    getMintPrice();

  }, [contract]);

  const [accountBalance, setAccountBalance] = useState();
  useEffect(() => {
    /*
     * :
     *  contract state  contract state balanceOf 
     *  currentAccount 
     *  accountBalance state 
     *  <div>: {accountBalance}</div>  accountBalance 
     * :  ethers.js  counter  bigNumber
     */
    if (!contract || !currentAccount) {
      return;
    }

    const getAccountBalance = async() => {
      const balanceOf_ = await contract.balanceOf(currentAccount.toString());
      setAccountBalance(balanceOf_.toNumber())
    };

    getAccountBalance();

  }, [contract]);

  const onMint = async() => {
    /*
     *  contract  mint 
     * 
     * 1. from : 
     * 2. value : mint  mintPrice  mintPrice 
     *     ethers.utils.formatEther  gwei
     *  Mint 
     */

    if (!contract || !currentAccount) {
      return;
    }

    console.log("mint price: " + mintPrice);

    const price = (mintPrice) ? ethers.utils.parseEther(mintPrice) : null;

    await contract.mint({
        from: currentAccount.toString(),
        value: price
    });
  };

  useEffect(() => {
    /*
     * :
     *  window.setInterval  contract  totalSupply  balanceOf 
     *  totalSupply state  accountBalance state 
     *  Mint totalSupply  accountBalance 
     * :  setInterval  useEffect  return  setInterval
     * : https://developer.mozilla.org/zh-TW/docs/Web/API/setInterval
     */
    let interval;
    if (contract) {
      interval = window.setInterval(async () => {
        const totalSupply_ = await contract.totalSupply();
        setTotalSupply(totalSupply_?.toNumber());

        if (currentAccount) {
          const balanceOf_ = await contract.balanceOf(currentAccount.toString());
          setAccountBalance(balanceOf_.toNumber())
        }
      }, 1000);
    }

    return () => {
      clearInterval(interval);
    };
  }, [contract]);

  return (
    <Layout>
      <h1>: Counter</h1>

      <div>
        <div>:</div>
        <div className="my-3">
          <div className="mb-1"> Mint : {totalSupply}</div>
          <div className="mb-1">: {accountBalance}</div>
          <div className="mb-1">Mint : {mintPrice} ETH</div>
          <button onClick={onMint}>Mint</button>
        </div>

        <div>
          <div>:</div>
          <ul>
            {/* 
                 [...new Array(totalSupply)]
                 map 
                 OwnerListItem Component
                 map  index 
                 index  OwnerListItem Component  tokenId  contract 
                 map  key
                :  totalSupply  undefined JSX  condition (if / else)
                1: https://stackoverflow.com/questions/47287177/how-to-loop-over-a-number-in-react-inside-jsx
                2: https://zh-hant.reactjs.org/docs/lists-and-keys.html
             */
              totalSupply && contract ?
              [...new Array(+totalSupply)].map((el, index) => <
                OwnerListItem
                key={index}
                tokenId={index}
                contract={contract} 
              />) : ""
             }
          </ul>
        </div>
      </div>
    </Layout>
  );
};

const OwnerListItem = ({ tokenId, contract }) => {
  const { currentAccount, provider } = useContext(BlockchainContext);

  const [ownerAddress, setOwnerAddress] = useState();
  useEffect(() => {
    /*
     * :
     *  contract  ownerOf 
     *  tokenId 
     *  ownerAddress state 
     *  {ownerAddress}  ownerAddress 
     */
    const findOwner = async() => {
      const ownerAddress = await contract.ownerOf(tokenId);
      setOwnerAddress(ownerAddress)
    };

    if (contract && tokenId != undefined) {
      findOwner();
    }
  }, [contract, tokenId]);

  return (
    <li>
      Token {tokenId}  {ownerAddress}
    </li>
  );
};

export default ERC721Assignment;
