import { useContext, useState, useEffect } from "react";
import { ethers } from "ethers";
import Layout from "../components/Layout";
import { BlockchainContext } from "../contexts/BlockchainContext";

//  Rinkeby Etherscan  ABI
const contractAddress = "0xa680F60AD58000F87Cdf9EA94A5c68ac8583c6EB";
const contractABI = [{"inputs":[],"name":"counter","outputs":[{"internalType":"uint256","name":"","type":"uint256"}],"stateMutability":"view","type":"function"},{"inputs":[],"name":"setIncrement","outputs":[],"stateMutability":"nonpayable","type":"function"}];

const EntryAssignment = () => {
  const { currentAccount, provider, messageTip } = useContext(BlockchainContext);
  const [contract, setContract] = useState();

  useEffect(() => {
    /*
     *  ethers.js  provider, contractAddress  contractABI  Contract 
     *  Contract  contract state
     * :  provider
     * 1.  signer Contract  connect  signer
     * 2.  block ()  gas fee Contract  gasLimit 
     * : https://docs.ethers.io/v5/getting-started/#getting-started--contracts
     */

    if (!provider) {
      return;
    }

    const signer = provider.getSigner();

    provider.getBlock().then((block) => {  

      const contract_ = new ethers.Contract(
        contractAddress,
        contractABI,
        provider,
        { gasLimit: block.getLimit }
      ); 
      setContract(contract_.connect(signer));
    });
  }, [provider]);

  const [counter, setCounter] = useState();

  useEffect(() => {
    /*
     * :
     *  contract state  contract state counter 
     *  counter state 
     *  <div>counter: {counter}</div>  counter 
     * :  ethers.js  counter  bigNumber
     */

    if (!contract) {
      return;
    }

    const getCounter = async () => {
      const counter = await contract.counter();
      setCounter(counter?.toNumber());
    };

    getCounter();

  }, [contract]);

  const onIncrement = async () => {
    /*
     *  contract  setIncrement 
     *  from 
     *  counter + 1 
     */

    if (contract && currentAccount) {
      await contract.setIncrement({ from: currentAccount.toString() });
    }
  };

  useEffect(() => {
    /*
     * :
     *  window.setInterval  contract  counter 
     *  counter state 
     *  counter + 1 counter 
     * :  setInterval  useEffect  return  setInterval
     * : https://developer.mozilla.org/zh-TW/docs/Web/API/setInterval
     */

    let interval;
    if (contract) {
      interval = window.setInterval(async () => {
        const _counter = await contract.counter();
        setCounter(_counter.toNumber());
      }, 1000);
    }

    return () => {
      clearInterval(interval);
    };

  }, [contract]);

  return (
    <Layout>
      <h1>: Counter</h1>

      <div>
        <div>{messageTip}</div>
        <div>{currentAccount}</div>
        <div>:</div>
        <div className="my-3">
          <div className="mb-1">counter: {counter}</div>
          <button onClick={onIncrement}>counter + 1</button>
        </div>
      </div>
    </Layout>
  );
};

export default EntryAssignment;
